.item {
    font-size: 1.2rem;
    list-style-type: none;
    padding: 15px 0;
    border-bottom: 1px solid #eaeaea;
    display: flow-root;
    align-items: center;
}

.checkbox { 
    margin-right: 15px;
}

.item button {
    font-size: 13px;
    background: #f1f3f4;
    border: none;
    cursor: pointer;
    float: right;
    outline: none;
    border-radius: 10px;
    height: 25px;
    width: 50px;
}

.textInput {
    width: 100%;
    padding: 10px;
    border: 1px solid #dfdfdf;
    border-radius: 5px;
}